ground-image:url("../images/services/underline.png");background-size:100% 100%;background-repeat:no-repeat}.wrap_company{display:block;position:relative;min-height:200px;margin:20px 0 30px 0}.company_box{display:block;width:200px;height:120px;margin:20px;box-shadow:1px 0 4px #AFAFAF}.wrap_company span:nth-child(1){background-image:url("../images/services/services_company/company01.jpg");background-size:cover;background-repeat:no-repeat}.wrap_company span:nth-child(2){background-image:url("../images/services/services_company/company02.jpg");background-size:cover;background-repeat:no-repeat}.wrap_company span:nth-child(3){background-image:url("../images/services/services_company/company03.jpg");background-size:cover;background-repeat:no-repeat}.wrap_company span:nth-child(4){background-image:url("../images/services/services_company/company04.jpg");background-size:cover;background-repeat:no-repeat}
