px}.send_us_message_txt{font-size:45px;font-weight:bold}.send_us_message_txt span{position:relative;height:2.5px;width:90px;background-color:rgba(203,48,241,0.8);left:5px;bottom:1px;display:block}.send_us_message_untxt{font-size:20px;padding:10px}.contact_form_wrap{display:block;min-height:150px;margin-top:20px}.input_half{display:block;min-height:150px}.input_half span{display:block;height:40px;width:80%;border-bottom:2px solid #884276;margin:15px 0 20px 0;overflow:hidden}.input_half span input{border:0;padding:5px 10px 5px 10px;width:100%;height:40px;font-size:18px}.contact_textarea{border-bottom:2px solid #884276;height:100px;font-size:18px;overflow:hidden;margin-top:25px}.contact_textarea textarea{width:100%;height:100px;resize:none;border:0;padding:10px}.cont_submit_message{display:block;width:150px;height:40px;margin-top:30px;border:2px solid transparent;text-align:center;padding-top:7px;letter-spacing:1px;font-family:"Big Shoulders Text",cursive;text-transform:uppercase;background-color:#421F39;cursor:pointer;transition:0.3s;color:#fff}.cont_submit_message:hover{background-color:#7C1762;border:2px solid #421F39}.fill_fields_msg{padding:10px;color:#FF2222}
